.site-nav {
  padding-top: 2em;
}

.site-nav > li {
  padding-bottom: 0.3em;
  padding-right: 2em;
  text-align: right;
}

.site-nav > li > span {
  color: var(--main-text-color);
  font-family: MyUniverse;
  font-size: 2.3em;
  text-decoration: none;
}

.site-nav > li > span:hover {
  color: var(--main-accent-color);
  cursor: pointer;
}

.site-nav > li > button {
  background: none;
  border: none;
  color: var(--main-text-color);
  font-family: MyUniverse;
  font-size: 2.3em;
  outline: none;
  text-decoration: none;
}

.site-nav > li > button:hover {
  background: none;
  border: none;
  color: var(--main-accent-color);
  cursor: pointer;
  outline: none;
}
